<template>
  <div class="welcome">
		<Alert>
			Vue.js 
			<template slot="desc">VUEX Mock </template>
		</Alert>

    <div class="userVisit" aria-label="" v-if="$store.state.power == 1">
        <Row :gutter="15" class="UserInfo">
        <Col :span="2">
          <div class="UserHeaderImg">
            <img :src="userinfo.pic" width="100%" alt="">
          </div>
        </Col>
        <Col :span="6">
          <h2 class="UserName">{{ userinfo.username }}</h2>
          <p class="UserSex">
            <Badge v-if="userinfo.sex == 0" count=""></Badge>
            <Badge v-if="userinfo.sex == 1" count=""></Badge>
            <Badge v-if="userinfo.sex == 2" count=""></Badge>
          </p>
          <h2 class="total"><span>{{ userinfo.total_consumption}}</span> </h2>
        </Col>
      </Row>
      <recommend-list><!----></recommend-list>
    </div>

    <div class="adminVisit" aria-label="" v-else-if="$store.state.power == 0">
      <div class="echarts" id="echarts" style="width:100%;height:400px;"></div>
    </div>

  </div>
</template>

<script>
import RecommendList from "@/components/GameServiceList/RecommendList"; // 

/**
	 echarts 
 */
const echarts = require("echarts/lib/echarts");
require("echarts/lib/chart/line");
require("echarts/lib/component/tooltip");
require("echarts/lib/component/title");

export default {
  name: "welcome",
  components: { RecommendList },
  data() {
    return {
      userinfo: {
        username: "Admin",
        pic: "https://i.loli.net/2017/08/21/599a521472424.jpg",
        sex: 0, // [0: male, 1: female, 2: undefined]
        total_consumption: 230.4
      }
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.echartsFuns();
    });
  },
  methods: {
    echartsRequestData() {
      //  or  echart 
      // datetime: X 
      // seeriesData {
      // 	name: ()
      // 	type: ()
      // 	stack: ()
      // 	data: ( X )
      //}
      let datetimes = [
        "2018/01/10",
        "2018/01/11",
        "2018/01/12",
        "2018/01/13",
        "2018/01/14",
        "2018/01/15",
        "2018/01/16",
        "2018/01/17",
        "2018/01/18"
      ];
      let Games = [""];
      let seriesData = [];
      Games.map(value => {
        let GameData = {
          name: value,
          type: "line",
          stack: "",
          data: []
        };
        for (var i = 0; i < datetimes.length; i++) {
          GameData.data.push(Math.round(Math.random() * 100));
        }
        seriesData.push(GameData);
      });
      return { datetimes, Games, seriesData };
    },

    // echart 
    echartsFuns() {
      let RequsetData = this.echartsRequestData();
      let myChart = echarts.init(document.getElementById("echarts"));
      let echarts_option = {
        title: {
          text: "Game plug "
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: RequsetData.Games
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: RequsetData.datetimes
        },
        yAxis: {
          type: "value"
        },
        series: RequsetData.seriesData
      };
      myChart.setOption(echarts_option);
    }
  }
};
</script>

<style lang="scss" scoped>
.UserInfo {
  .UserHeaderImg {
    img {
      border-radius: 5px;
    }
  }
  .total {
    margin: 15px 0px;

    span {
      color: tomato;
    }
  }
}
.recommend {
  padding: 15px 0px;
}
</style>



