<template>
  <div class="recommend">
      <h2 class="title"></h2>
      <Row :gutter="15">
          <Col :span="2" v-for="item in list" :key="item._id">
            <div class="card">
                <img :src="item.pic" width="100%" alt="">
                <div class="card-caption">
                    <h4 class="product-name">{{ item.product_name }} <span class="product-price">{{ item.price }}</span></h4>
                    <p>{{ item.readme }}</p>
                </div>
            </div>
          </Col>
          <Col :span="2">
            <div class="card more"></div>
          </Col>
      </Row>
  </div>
</template>

<script>
export default {
  name: 'recommend',
  data () {
      return {
          list: [
              {
                  _id: Math.random(),
                  product_name: '',
                  readme: '*1',
                  price: 2,
                  pic: 'data:image/svg+xml;base64,'
              },{
                  _id: Math.random(),
                  product_name: '',
                  readme: '*1',
                  price: 2,
                  pic: 'data:image/svg+xml;base64,'
              },{
                  _id: Math.random(),
                  product_name: '',
                  readme: '*1',
                  price: 2,
                  pic: 'data:image/svg+xml;base64,'
              },{
                  _id: Math.random(),
                  product_name: '',
                  readme: '*1',
                  price: 2,
                  pic: 'data:image/svg+xml;base64,'
              },{
                  _id: Math.random(),
                  product_name: '',
                  readme: '*1',
                  price: 2,
                  pic: 'data:image/svg+xml;base64,'
              }
          ]
      }
  }
}
</script>

<style scoped lang="scss">
    .card {
        background: white;
        position: relative;
        cursor: pointer;
        overflow: hidden;

        .card-caption {
            position: absolute;
            top: 80%;
            background: rgba(255, 255, 255, .75);
            right: 0px;
            left: 0px;
            transition: .3s;
            -moz-transition: .3s;
            -webkit-transition: .3s;

            .product-name {
                padding: 4px 6px;
                font-weight: normal;

                .product-price {
                    float: right;
                    color: tomato;
                }
            }
            p {
                padding: 6px;
            }
        }

        &:hover {
            .card-caption {
                top: 0px;
                bottom: 0px;
                overflow-x: hidden;
                overflow-y: auto;
            }
        }
    }
    .card.more {
        height: 130px;
        background: #f2f2f2;
        position: relative;

        &::before {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            -moz-transform: translate(-50%, -50%);
            -webkit-transform: translate(-50%, -50%);
            font-size: 20px;
            color: #888;
        }
    }
</style>



